<script>
// @ is an alias to /src
// import HelloWorld from '@/components/HelloWorld.vue';

export default {
  name: 'Home',
  data() {
    return {
      tianqi: {},
      pushBoxIndex: 1,
      category: [],
      pushData: [],
      options: {
        afterLoad: this.fn2,
        onLeave: this.fn1,
        licenseKey: 'YOUR_KEY_HEERE',
        menu: '#menu',
        anchors: ['page1', 'page2', 'page3', 'page4', 'page5', 'page6'],
        sectionsColor: [
          '#41b883',
          '#ff5f45',
          '#0798ec',
          '#0798ec',
          '#0798ec',
          '#ff5f45',
        ],
      },
    };
  },
  created() {
    this.gettianqi();
    this.getData();
  },
  mounted() {
    /* eslint-disable no-new */
    // eslint-disable-next-line no-undef
    new Swiper('.links', {
      slidesPerView: 4,
      spaceBetween: 30,
      slidesPerGroup: 4,
      autoplay: true,
      loop: true,
      loopFillGroupWithBlank: true,
    });
  },
  methods: {
    async getData() {
      const res = await this.$axios.post('/one/api/getOneIndex');
      const { success, category, pushData } = res.data;
      console.log(res.data);
      if (success) {
        this.category = category;
        this.pushData = pushData;
      }
    },
    fn1() {
      const arr1 = document.querySelectorAll('.animate__animated');
      arr1.forEach((r) => {
        try {
          const st1 = r.className
            .split(' ')
            .find((s) => s.startsWith('animate__') && s !== 'animate__animated');
          r.classList.remove(st1);
        } catch (e) {
          console.log(e);
        }
      });
    },
    fn2(a, b) {
      setTimeout(() => {
        const arr2 = b.item.querySelectorAll('.animate__animated');
        arr2.forEach((r) => {
          const { action } = r.dataset;
          if (action) r.classList.add(action);
        });
      }, 0);
    },
    async gettianqi() {
      const res = await this.$axios.get('https://v0.yiketianqi.com/api', {
        params: {
          appid: '42363995',
          appsecret: '2QFwmnUt',
          version: 'v61',
        },
      });
      console.log(res);
      this.tianqi = res.data;
      console.log(this.tianqi);
    },
  },
};
</script>
